header
{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container
{
    text-align: center;
    height: 100%;   
    position: relative;
}

/* CTA css */
.cta{
    margin-top: 2.5rem;
    display: flex;
    justify-content: center;
    gap: 1.5rem;
}


/* header social */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.7rem;
    position: absolute;
    left: 0%;
    bottom: 3.2rem  ;
}
.header__socials::after
{
    content: "";
    width: 1px;
    height: 2rem;
    background-color: var(--color-primary);
}

/* ME  */

.me
{
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 10rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1rem 1rem;

}
/* scroll down */
.scroll__down
{
    position: absolute;
    right: -2.3rem;
    bottom: 4.5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.8rem;
}
/* media quiries for med devices */

@media screen and (max-width:1024px) {
    header{
        height: 60vh;
    }
    
}
/* media quiries for small devices */

@media screen and (max-width:600px) {
    header{
        height: 100vh;
    }
    .header__socials,.scroll__down{
        display: none;
    }
    
}